@import url(https://fonts.googleapis.com/css?family=Montserrat:400,500,600,700|Poppins:300,400);
@import url(https://fonts.googleapis.com/css2?family=Merienda+One&display=swap);
@import url(https://fonts.googleapis.com/css?family=Montserrat:400,500,600,700|Poppins:300,400);
/* volunteer stylings */

.no-padding {
  padding: 0 !important;
}

.volunteer-title {
  font-family: 'Montserrat', sans-serif;
  font-size: 32px;
  text-align: center;
  text-transform: capitalize;
}

.volunteer-title span {
  border-bottom: 5px solid #ef3d32;
}

.volunteer-cotes {
  font-family: 'Montserrat', sans-serif;
  font-size: 45px;
  font-weight: bold;
  text-align: center;
  margin-top: 50px;
  margin-bottom: 50px;
  color: #454545;
}

.volunteer-name {
  text-align: center;
  font-family: 'Montserrat', sans-serif;
  font-weight: bold;
  margin-top: 10px;
  margin-bottom: 5px;
}

.volunteer-proffession {
  text-align: center;
  font-family: 'Montserrat', sans-serif;
  font-weight: bold;
  color: #555555;
  font-size: 20px;
  margin-top: 5px;
  margin-bottom: 5px;
}

.volunteer .card {
  margin: 10px 0;
}

.volunteer img {
  object-fit: cover;
}

.volunteer img:hover {
  transform: scale(1.01);
}

.volunteer-btn {
  margin-top: 80px;
  margin-bottom: 30px;
  width: 240px;
  height: 70px;
  color: white;
  font-weight: bold;
  font-size: 18px;
  font-family: 'Montserrat', sans-serif;
  background-color: #ef3d32 !important;
  border: none !important;
}

/* footer styling */

.footer {
  background-color: #121212;
  color: #fff;
  font-family: 'Montserrat', sans-serif;
  margin-top: 30px;
}

.first-footer {
  padding: 80px 0;
}

.footer img {
  object-fit: cover;
}

.first-footer p {
  font-weight: 500;
  line-height: 2em;
  padding-left: 50px;
}

.second-footer {
  border-top: 1px solid #555555;
  padding: 30px 0;
}

.second-footer i {
  color: #ef3d32;
}

.second-footer ul li {
  list-style-type: none;
  margin: 10px 0;
}

.second-footer ul li a {
  color: white;
}

.second-footer ul li a:hover {
  text-decoration: none !important;
}
.second-footer input {
  width: 100%;
  height: 50px;
  background-color: #333333;
  border: none;
  padding: 10px;
  margin-bottom: 20px;
  color: #fff !important;
}

.second-footer button {
  width: 60%;
  background-color: #ef3d32;
  border: none;
  height: 40px;
  color: #fff !important;
}

.third-footer {
  text-align: center;
  font-weight: 500;
  border-top: 1px solid #555555;
  padding: 30px 0;
  margin-top: 20px;
}

/* styling for gallery */

.gallery {
  margin: auto;
  text-align: center;
  padding-top: 30px;
  font-family: 'Montserrat' sans-serif;
  margin-bottom: 85px;
  margin-top: 50px;
  width: 80%;
}

.gallery h2 {
  font-size: 42px;
  font-weight: bold;
  margin-bottom: 35px;
}

.gallery h2 span {
  padding-bottom: 2px;
  border-bottom: 5px solid #ef3d32;
}

.gallery h4 {
  color: #555555;
  margin-bottom: 40px;
}

.gallery .row {
  padding: 30px 50px 0 50px;
}

.row .col-md-4 {
  margin-bottom: 25px;
}

#gallerysecondh2 {
  font-size: 38px;
  color: #333333;
  padding-top: 30px;
}

.gallery p {
  font-weight: 500;
  font-size: 18px;
  color: #555555;
}

.gallery-btn {
  width: 200px;
  height: 70px;
  color: white;
  background-color: #ef3d32;
  border: none;
  margin-top: 20px;
}

/* donor registration form styling */

.donor-registration {
  font-family: 'Montserrat', sans-serif;
  margin: 30px 0;
}

.donor-registration-title {
  width: 67%;
  margin: auto;
  height: 100px;
  background-color: #ef3d32;
  text-align: center;
}

.donor-registration-title h4 {
  font-size: 40px;
  font-weight: 600;
  padding-top: 20px;
  color: white;
}

.donor-registration p {
  text-align: center;
  font-weight: 500;
  font-size: 18px;
  color: #454545;
  margin-top: 20px;
  margin-bottom: 30px;
}

.donor-registration .card {
  box-shadow: 1px 1px 4px 1px rgba(0, 0, 0, 0.5);
  padding: 20px;
  margin: 40px 0;
}

.donor-registration form label {
  font-weight: 500;
}

.donor-registration button {
  width: 180px;
  height: 50px;
  background-color: #ef3d32;
  color: white;
  font-size: 20px;
  font-weight: 500;
  border: none;
}

.donor-registration form h5 {
  margin-top: 35px;
}

/* contact us styleing */

.contact {
  font-family: 'Montserrat', sans-serif;
}

.home-contact {
  background-image: linear-gradient(rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4)),
    url(/static/media/header-bg.8e98818a.jpg);
  background-attachment: fixed;
  width: 100%;
  height: 270px;
}

.home-contact h4 {
  color: white;
  font-size: 34px;
  font-weight: bold;
  text-transform: uppercase;
  text-align: center;
  padding-top: 80px;
}

.home-contact p {
  text-align: center;
  margin-top: 15px;
  font-size: 19px;
  font-weight: 500;
  color: white;
  text-transform: uppercase;
}

.home-contact p a {
  color: white;
}

.home-contact p a:hover {
  color: #ef3d32;
  text-decoration: none;
}

.contact-detail {
  width: 80%;
  margin: auto;
  margin-top: 100px;
}

.contact-detail h4 {
  font-size: 26px;
  font-weight: 700;
  color: #454545;
}

.contact-detail h4 span {
  border-bottom: 4px solid #ef3d32;
}
.contact-detail i {
  background-color: #ef3d32;
  color: white;
  padding: 7px 7px;
  margin-right: 20px;
}

.form-map {
  margin: auto;
  width: 80%;
  margin-top: 100px;
}
.form-map .col-md-6 .row {
  width: 100%;
  margin: auto;
  background-color: #dddddd;
  height: 5px;
  margin-top: 10px;
  margin-bottom: 40px;
}
.form-map .col-md-6 .col-md-3 {
  background-color: #ef3d32;
}

.form-map input {
  width: 100%;
  height: 50px;
  font-weight: 600;
  padding: 10px;
  margin-bottom: 20px;
  border: 1px solid grey;
}

.form-map textarea {
  width: 100%;
  height: 100px;
  font-weight: 600;
  padding: 10px;
}

.form-map button {
  height: 60px;
  width: 180px;
  color: white;
  background-color: #ef3d32;
  border: none;
  font-weight: 600;
  margin-top: 30px;
}

* css for header section */

/* Navigation.jsx */

.navbar img {
  height: 60px;
}

.align-right {
  margin-left: auto;
  margin-right: 0;
}

.menu-nav-bar .navbar-nav .nav-item .nav-link {
  color: #000000;
  font-weight: bold;
}

.menu-nav-bar .navbar-nav .nav-item .nav-link:hover {
  color: #ef3d32;
}

.navbar-expand-lg .navbar-nav .dropdown-menu {
  position: absolute;
  right: 0;
  left: auto;
  margin-top: -30px;
}

/* Social Media */

.white-font {
  color: #ffffff;
}

.white-background {
  background-color: #ffffff;
}
.social-media-nav {
  background-color: #ef3d32;
  padding: 10px;
}

.align-middle {
  margin: 0;
  padding: 0;
}
.social-media-section {
  float: right;
}

.social-media-section .fa {
  color: #666;
  width: 24px;
  background-color: #ffffff;
  padding: 6px;
  margin: 3px;
  text-align: center;
  font-size: 14px;
  border-radius: 100%;
  height: 24px;
}

/* loginForm */

.login-container {
  margin: 100px auto;
}

.login-form {
  margin-top: 50px;
  /* Banner styling */
}

.carousel .carousel-indicators li {
  width: 10px;
  height: 10px;
  border-radius: 90%;
}

.carousel-caption {
  font-family: 'Montserrat', sans-serif;
}

.carousel-caption h5 {
  font-size: 50px;
  font-weight: 700;
  text-transform: uppercase;
  text-align: left;
  margin-bottom: 20px;
}

.carousel-caption p {
  font-size: 28px;
  font-weight: 400;
  text-align: left;
}

.slider-btn {
  height: 70px;
  width: 200px;
  color: white;
  background-color: #ef3d32;
  border: none;
  font-size: 24px;
  font-weight: 500;
  text-transform: capitalize;
}

.slider-btn-wrapper {
  float: left;
  margin-bottom: 20%;
}

/* intro section styling */
.intro {
  margin-top: 100px;
  margin-bottom: 30px;
  font-family: 'Montserrat', sans-serif;
}
.intro img {
  max-width: 100%;
  max-height: 100%;
}
.intro h4 {
  font-size: 42px;
  color: #333333;
  font-weight: 650;
  margin-bottom: 5px;
}
.intro h6 {
  color: #565656;
  font-size: 17px;
  font-weight: 200;
  margin-bottom: 25px;
  margin-top: 20px;
}

.intro p {
  color: #565656;
  font-size: 17px;
  font-weight: 200;
}

.intro p i {
  color: #ef3d32;
  font-size: 12px;
  margin-right: 20px;
}

.intro .underline {
  height: 4px;
  width: 70px;
  background-color: #ef3d32;
  margin-left: 5px;
}

/* page not found styling */
.notfound h2 {
  font-size: 48px;
}

.notfound h2 span {
  color: #ef3d32;
}

/* Announcement styling */
.announcement {
  width: 100%;
  font-family: 'Montserrat', sans-serif;
}

.announcement .title {
  margin: 50px 0;
  font-size: 38px;
  font-weight: 500px;
  color: #333333;
  padding-bottom: 15px;
  text-align: center;
}

.announcement .title span {
  border-bottom: 4px solid #ef3d32;
}
.announcement-list {
  width: 80%;
  margin: auto;
}

/* .announcement-list .card,
.volunteer .container .card {
  height: 375px !important;
} */
.announcement .card {
  width: 285px !important;
}

.announcement .card img {
  width: 285px;
  height: 200px;
  object-fit: cover;
}

/* announcement detail css */
.announcement-detail {
  font-family: 'Montserrat', sans-serif;
  width: 90%;
  margin: auto;
  margin-top: 50px;
}

.announcement-detail h4 {
  font-size: 32px;
  font-weight: 500;
  margin-bottom: 30px;
}

.announcement-detail .imagediv {
  height: 400px;
}

.announcement-detail p {
  margin-top: 40px;
  line-height: 1.5rem;
}

.announcement-detail img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

/* profile Styling */
.profile-img {
  text-align: center;
}
.profile-img img {
  width: 70%;
  height: 100%;
}
.profile-img .file {
  position: relative;
  overflow: hidden;
  margin-top: -20%;
  width: 70%;
  border: none;
  border-radius: 0;
  font-size: 15px;
  background: #212529b8;
}

.profile-img .file input {
  position: absolute;
  opacity: 0;
  right: 0;
  top: 0;
}

.profile-detail .profile-edit-btn {
  width: 70%;
  /* text-align: center; */
  padding: 5px;
  background: #ece0e0;
}

.EditDetail .container {
  margin-top: 30px;
}

.EditDetail a {
  background: #ffffff;
  color: black;
  margin: 0 10px;
}

.EditDetail a:active {
  background: #ef3d32;
  color: white;
  margin: 0 10px;
}

.btn-submit {
  background: #ef3d32;
  color: #ffffff;
}
/* search donor section styling */
.donor-search {
  width: 90%;
  margin: auto;
  margin-top: 60px;
}
.donor-search h4 {
  text-align: center;
  font-size: 32px;
  font-family: 'Montserrat', sans-serif;
  font-weight: 600;
  margin-bottom: 30px;
}
.donor-search form {
  justify-content: center;
  margin-bottom: 40px;
}
.donor-search form input,
.donor-search form select,
.donor-search form button {
  width: 200px !important;
  height: 50px !important;
  font-size: 16px !important;
}
.donor-search form input:first-child {
  margin-right: 10px;
}
.donor-search form button {
  margin-left: 10px;
  background-color: #ef3d32;
  color: #fff;
}
.donor-search .donor-lists .row {
  justify-content: center;
}

.donor-search .donor-lists .card {
  margin: 10px 20px;
}
.donor-search .donor-lists img {
  width: 280px;
  height: 340px;
  object-fit: cover;
}
.donor-search .donor-lists .card h5 {
  text-align: center;
  font-weight: 600;
}
.donor-search .donor-lists .card p {
  text-align: center;
  font-weight: 600;
}

.set-font {
  font-family: 'Montserrat', sans-serif;
  font-size: 16px;
}

@media (max-width: 992px) {
  /* navbar dropdown styling */
  .navbar-expand-lg .navbar-nav .dropdown-menu {
    position: relative;
    right: 10;
    left: 10;
    margin-top: 0;
  }

  .announcement-list {
    width: 90%;
  }

  .announcement-list .card {
    width: 100% !important;
  }

  /* Carousel styling*/
  .carousel-caption p {
    font-size: 18px;
  }
  .carousel-caption h5 {
    font-size: 30px;
  }

  .slider-btn-wrapper {
    margin-bottom: 10%;
  }

  /* login - logout button */
  .login-btn {
    margin: 0;
    padding: 0;
  }
}

@media (max-width: 768px) {
  .first-footer p {
    padding-left: 0px;
  }

  .announcement-list {
    width: 70%;
  }

  /* Carousel styling*/
  .carousel-caption {
    padding: 0 !important;
  }

  .carousel-caption h5 {
    font-size: 20px;
  }

  .slider-btn-wrapper {
    margin-bottom: 0;
    font-size: 18px;
  }

  .slider-btn {
    font-size: 18px;
    height: 50px;
  }

  /* Volunteer Section */

  .volunteer-cotes {
    font-size: 36px;
  }

  /* map section */

  .form-map button {
    margin-bottom: 30px;
  }

  .profile-detail .profile-edit-btn {
    margin-top: 30px;
    background-color: #ef3d32;
    color: #fff;
  }
}

.sticky{
  position: fixed;
  background-color:#ef3d32;
  top: 0;
  width: 100%;
  height:60px;
  color: white;
  z-index: 99;
}
.dashboard{
    width:100%;
    font-family: "Montserrat", sans-serif;
    text-decoration: none;
    margin: 0;
    padding: 0;
}

/* sidebar */
.sidebar{
    position: fixed;
    top:0;
    left:0;
    height: 100vh;
    width:270px;
    /* background-color: #EF3d32; */
    background-color: #c91c1c;
    border-right: #dacdcd solid;
    

}

.sidebar .logo{
    width:100%;
    font-size: 32px;
    font-weight: 700;
    text-align: center;
    margin-top: 20px;
}

.sidebar .logo span{
    color: white;
}

.sidebar .menu-item {
    height: 40px;
    width:100%;
    padding: 10px 5px 10px 20px;
    font-weight: 600;
    cursor:pointer;
}

.sidebar .menu-item:hover{
    color:#EF3d32 !important;
    background: #ffffff;
    
}

.sidebar .menu-item a{
    color: #fff;
}
.sidebar .menu-item a:hover{
    color: #EF3d32;
    text-decoration: none;
}




/* admin header */
.admin-header{
    position: fixed;
    top:0;
    left:270px;
    width: calc(100% - 270px);
    height: 70px;
    background: #ffebee;
    display:flex;
    padding: 0 20px;
    z-index: 8;
    border-bottom: 1px solid rgb(209, 207, 207);
}

.admin-header .logo{
    font-size:20px;
    font-weight: 900;
    flex:6 1;
    margin-top: 20px;
}

.admin-header .logo p a{
    color: #343434;
    text-decoration: none;
}
.admin-header .logo p a:hover{
    color: #000;
}

.admin-header .logo p{
    cursor:pointer;
}

.admin-header .header-menu{
    flex:6 1;
}


.admin-header .header-menu i{
    float: right;
    margin-right: 40px;
    margin-top: 25px;
    cursor: pointer;
}
/* main content */
.main-content{
    position: static;
    margin-top:70px;
    margin-left:270px;
    width: calc(100% - 270px);
    background-color: #fff;
    z-index: -8;
}


/* mobile navigation */
.mobile-navigation{
    display:flex;
    margin-top:70px;
    width: 100%;
    background: #e91e63;
    display: none;
}

.nav-btn{
    width: 100%;
    height: 60px;
}

.mobile-navigation img{
    height: 40px;
    width:40px;
    border-radius: 50%;
    margin-top:10px;
    margin-left: 20px;
}

.mobile-navigation .nav-btn i{
    cursor: pointer;
    color: #ffffff;
    font-size: 24px;
    float: right;
    margin-right: 5%;
    margin-top: 20px;
}

.mobile-nav{
    /* background: #ece6e6; */
    background: #ffffff;
    z-index:8;
}

.mobile-navigation .menu-item {
    height: 40px;
    width:100%;
    padding: 10px 5px 10px 20px;
    color: #EF3d32;
    font-weight: 600;
    cursor: pointer;
}

.mobile-navigation .menu-item:hover{
    background: #EF3d32;
    color: #ece6e6;
}

.mobile-navigation .menu-item a{
    color: #EF3d32;
    text-decoration: none;
}
.mobile-navigation .menu-item a:hover{
   color: white;
}

/* graphs styling */

.dashboard-graphs{
    position: static;
}

.quick-info h4{
    font-size: 28px;
    font-weight: 600;
    padding:10 0;
}
.dashboard-graphs .col-md-4 .card{
    height: 170px;
}

.dashboard-graphs .col-md-4 .card h5{
    padding: 10px;
    font-size: 32px;
    font-weight: 500;
    color: white;
}

.dashboard-graphs .col-md-4 .card p{
    color: white;
    padding-left: 10px;
}

/* adminslider css */
.admin-slider h4{
    margin-top: 20px;
    font-weight: 400;
}
.admin-slider img{
    width: 100px;
}

/* admin gallery css */
.admin-gallery{
    width:96%;
    text-align: center;
}

.admin-gallery h4{
    text-align: left;
    padding: 10px;
}
.admin-gallery h4 span{
    color: #fff;;
    font-size: 14px;
    float: right;
    background-color: #EF3d32;
    padding:5px;
    cursor: pointer
}
.admin-gallery img{
    height: 200px;
}

.admin-gallery table i{
    color:red;
    cursor: pointer;
    padding: 10px;
    font-size: 20px;
}

.admin-gallery table i:first-child{
    color: blue;
}

/* add gallery */
.add-gallery form{
    width: 80%;
}

/* admin-donor*/
.admin-donor h4{
    font-size: 26px;
    font-weight: 500;
    padding-top: 30px;
    padding-bottom: 10px;
}

.admin-donor h4 span{
    background-color: #EF3d32;
    padding: 0 10px 5px 10px;
    float: right;
    cursor: pointer;
}
.admin-donor h4 span a{
    font-size: 14px;
    color: white;
    text-decoration: none;;
}

.admin-donor table i{
    font-size: 16px;
    padding: 5px 10px;
    cursor: pointer;
}

/* donor detail page css */
.donor-detail{
    padding: 30px 10px;
}

.donor-detail img{
    width: 200px;
    height: 200px;
    object-fit: cover;
    border-radius: 50%;
}

.donor-detail h2{
    padding-top: 30px;
    font-size: 36px;
    font-weight: 600;
}

.donor-detail p{
    color: #343434;
    font-size:15px;
}

.donor-detail i{
    color: #e91e63;
}

.donor-detail h4{
    font-weight: 600;
}

/* announcements */
.announcement-lists{
    padding: 25px 5px;
}
.announcement-lists h4{
    font-size: 26px;
    font-weight: 600;
}
.announcement-lists table i{
    padding: 5px 10px;
    cursor: pointer;
}
.announcement-lists span{
    font-size: 14px;
    background-color: #EF3d32;
    padding:5px 7px;
    float: right;
    cursor: pointer;
}
.announcement-lists span a{
   color:white;
} 
.announcement-lists span a:hover{
    text-decoration: none;
 } 

 .announcement-detail h4{
    padding: 20px 5px;
 }

 /* stock */
 .stock h4{
    padding: 20px 5px;     
 }

 .stock table i{
     padding: 3px 6px;
 }

 .stock h4 span{
    font-size: 14px;
    background-color: #EF3d32;
    padding:5px 7px;
    float: right;
    cursor: pointer;
}

.stock  h4 a span{
    color:white;
 } 

.stock h4 a span:hover{
     text-decoration: none;
  } 

.stock table i{
    cursor: pointer;
}

/* request styling */
.request h4{
    padding: 20px 5px;
}
.request table i{
    padding: 5px 10px;
    cursor: pointer;
}

/*media queries */

@media(max-width: 768px){
    .sidebar{
        display: none;
    }

    .admin-header{
        width:100%;
        left: 0;
        background-color: #EF3d32;
        border:none;
    }

    .admin-header .header-menu i{
        float: right;
        margin-right: 25px;
        margin-top: 25px;
    }

    .main-content{
        margin-left:0;
        margin-top:0;
        width: 100%;
    }

    .mobile-navigation{
        display: block;
        margin-top: 70px; 
    }

    .admin-slider img{
        width: 50px;
    }

    .admin-slider th, td{
        font-size: 12px;
    }

    .admin-gallery img{
        height: 80px;
    }

    .add-gallery{
        width:96%;
        margin: auto;
    }
    .add-gallery form{
        width: 80%;
        margin:auto;
    }
    .add-gallery h4{
        text-align: center;
        font-size: 18px;

    }
    /* update slider */
    .update-slider h4{
        font-size: 16px;
        margin-top: 10px;
    }
    /* admin donors */
    .admin-donor{
        width: 90%;
    }
    .admin-donor h4{
        font-size: 16px;
        font-weight: 3500;
        padding-top: 15;
        padding-bottom: 5px;
    }
    .admin-donor table i{
        font-size: 11px;
        padding: 3px 4px;
    }

    /* donor detail page css */
    .donor-detail{
        padding: 15px 5px;
    }

    .donor-detail img{
        width: 100px;
        height: 100px;
        object-fit: cover;
        border-radius: 50%;
    }

    .donor-detail h2{
        padding-top: 10px;
        font-size: 24px;
        font-weight: 400;
    }

    .donor-detail p{
        font-size:13px;
    }

    /* announcements */
    .announcement-lists{
        padding: 15px 2px;
    }
    .announcement-lists h4{
        font-size: 18px;
        font-weight: 600;
    }
    .announcement-lists table i{
        padding: 3px 5px;
    }

}


